@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
   background-image: linear-gradient(to right, transparentize(v.$primario, .1), transparentize(v.$primario, .1)), url(../img/header_bg.jpg);
   background-size: cover;
   background-position: center;
   padding: 3rem 0;
   position: relative;

   @include m.tablet { 
        padding: 3rem 0 20rem 0;
    }

   &__barra{
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
            
         }
   }
    &__logo{
        width: 25rem;
     
    }
    &__contenido{
        @include m.tablet { 
            margin-top: 10rem;
            max-width: 50rem;
         }
    }
    &__heading{
        color: v.$blanco;
        text-align: center;

        @include m.tablet { 
            text-align: left;
         }
    }
    &__texto{
        color: v.$blanco;
        display: none;
        @include m.tablet { 
            display: block;
         }
    }
    &__imagen{
        display: none;
        @include m.tablet { 
            display: block;
            position: absolute;
            right: 0;
            bottom: -5rem;
            max-width: 45rem;
            border-top-left-radius: 2rem;
            border-bottom-left-radius: 5rem;
         }
         @include m.desktopXL{
            max-width: 60rem;
         }
    }
}
.navegacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;

    @include m.tablet { 
        flex-direction: row;
        gap: 2rem;
        margin-top: 0;
     }
    &__link{
        color: v.$blanco;
        font-size: 1.8rem;
        margin-bottom: 2rem;
        text-transform: uppercase;

        &:last-of-type{
            margin-bottom: 0;
        }
        &:hover{
            color: #b0d023;
            box-shadow: 0px 6px 20px rgb(0 0 0 / .40);
        }
        @include m.tablet { 
            margin-bottom: 0;
         }
    }
}
